.popup__container {
  display: block;
  position: relative;
  width: 430px;
  margin: 0;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (max-width: 530px) {
  .popup__container {
    width: 282px;
    min-height: 322px;
  }
}
